import {RecoverComponent} from './components/recover/recover.component';
import {RegistrationComponent} from './components/registration/registration.component';

@NgModule({
  imports: [
    MaterialModule
  ],
  declarations: [
    LoaderComponent,
    LoginComponent,
    RecoverComponent,
    RegistrationComponent
  ],
  exports: [
    LoaderComponent,
    LoginComponent,
    RecoverComponent,
    RegistrationComponent
  ],
  providers: [],
})
export class SharedModule {
}



