import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MdButtonModule, MdIconModule, MdInputModule, MdTabsModule } from '@angular/material';

@NgModule({
  imports: [
    CommonModule,
    MdIconModule,
    MdButtonModule,
    MdInputModule,
    MdTabsModule
  ],
  exports: [
    MdIconModule,
    MdButtonModule,
    MdInputModule,
    MdTabsModule
  ]
})

export class MaterialModule { }



