import {Component} from '@angular/core';

@Component({
  moduleId: module.id,
  selector: 'lesson',
  templateUrl: 'lesson.component.html',
  styleUrls: ['lesson.component.sass']
})

export class LessonComponent {
  public isFirstTabActive: boolean = true;
  public isSecondTabActive: boolean = false;

  public activateTab(val: number): any {
    if (val === 1) {
      if (this.isFirstTabActive === false) {
        this.isFirstTabActive = true;
        this.isSecondTabActive = false;
      } else {
        return;
      }
    } else {
      if (this.isSecondTabActive === false) {
        this.isSecondTabActive = true;
        this.isFirstTabActive = false;
      } else {
        return;
      }
    }
  }
}



