import { Component, Output } from '@angular/core';
import { NgClass } from '@angular/common';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.sass'],

})
export class HeaderComponent {
  @Output() public isLoginFormActive: boolean = false;
  @Output() public isRegFormActive: boolean = false;
  public collapseMenu: boolean = true;

  public toggleLoginForm(): void {
    this.isLoginFormActive = !this.isLoginFormActive;
  }

  // public openLoginForm(): void {
  //   this.isLoginFormActive = !this.isLoginFormActive;
  // }

  public toggleMenu(): void {
    this.collapseMenu = !this.collapseMenu;
  }


  public toggleRegForm(): void {
    this.isRegFormActive = !this.isRegFormActive;
  }

  // public ngOnChanges(changes: any): any {
  //   this.isLoginFormActive = changes;
  // }


}




