import { Inject, Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';


@Injectable()
export class LandingService {

  public constructor(
    @Inject('DOMAIN') private _domain: string,
    private http: Http
  ) {
  }

  public getLanding(): Observable<any> {

    return this.http.get(`${this._domain}/api/courses/`)
      .map( (res: any) => {
        return res.json();
      })
      .catch( (err: Error): any => {
        return Observable.of({ success: false, message: 'Server Error -> LandingService'});
      });

  }
}



